import React from "react";
import { connect } from "react-redux";
import { tripAction } from "./redux/actions/tripAction";
import loadingGif from "./assets/loading.svg";
import { TripItem } from "./tripItem/tripItem";
import Pagination from "./pagination/pagination";
import {
  StyledTripWrapper,
  StyledAppContainer,
  StyledAppTitle
} from "./App.styled";

export class App extends React.Component {
  componentDidMount() {
    const { currentPage } = this.props;
    console.log(currentPage, currentPage);
    this.props.tripAction(currentPage === 1 ? 0 : currentPage);
  }

  render() {
    const { data, fetchState } = this.props;
    return (
      <StyledAppContainer>
        {fetchState === "loading" && <img src={loadingGif} alt="loading" />}
        {fetchState === "success" && (
          <StyledTripWrapper>
            <Pagination />
            <StyledAppTitle>Trips Management</StyledAppTitle>
            {data.trips.map(trip => (
              <TripItem key={trip.trip_id} tripData={trip} />
            ))}
          </StyledTripWrapper>
        )}
      </StyledAppContainer>
    );
  }
}

const mapStateToProps = state => {
  return {
    data: state.tripReducer.data,
    fetchState: state.tripReducer.fetchState,
    currentPage: state.paginationReducer.currentPage
  };
};

const mapDispatchToProps = {
  tripAction
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);
